* {
  margin: 0;
  padding: 0;
  font-family: "Roboto", sans-serif;
  box-sizing: border-box;
  font-weight: 300;
  text-align: center;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
body {
  margin: 10px;
  background-color: #130f40;
  background-image: linear-gradient(315deg, #000000 0%, #130f40 74%);
  background-repeat: no-repeat;
  background-attachment: fixed;
  color: white;
}
.form {
  display: flex;
  flex-direction: column;
  height: 140px;
  align-items: center;
  justify-content: center;
  width: 80%;
  margin: auto;
}
.label {
  font-size: 16px;
  align-self: start;
  padding: 2px;
  font-weight: 100;
}
.search {
  padding: 10px;
  width: 100%;
  border-radius: 3px;
  border: none;
}
button {
  margin: 10px auto;
  padding: 10px;
  width: 100%;
  border-radius: 3px;
  border: none;
  background-color: white;
  color: black;
  font-size: 17px;
}
.card {
  border: none;
  background-color: #000000;
  background-image: linear-gradient(147deg, #2c3e50 0%, #000000 74%);
  border-radius: 6px;
  margin: 20px;
  box-shadow: 0px 1px 10px 3px rgba(120, 120, 120, 0.1);
  padding: 10px;
}
.poster {
  width: 80%;
  border-radius: 4px;
}
.title {
  font-size: 30px;
  font-weight: 400;
}
small {
  font-size: 14px;
  font-weight: 500;
}
.desc {
  font-weight: 100;
}
